@import url('./1.css');
@import url('./codemirror.css');
@import url('./blackboard.css');
@import url('./boxicons.css');
@import url('./react-metismenu-standart.min.css');
@font-face {
  font-family: px-sans;
  src: url('./font/px_sans_nouveaux.woff');
}
.logo{
  position: fixed;
  bottom: 0%;
  right: 0%;
  height: 20px;
  width: 20px;
}
.App {
  text-align: center;
}
.CodeMirror{
  border-color: rgb(39, 37, 37);
  border-style: solid;
  border-width: 5px;
  display: block;

}
.sidebar {
  padding: 12px 20px;
  margin: 0;
  padding: 0;
  width: 200px;
  background-color: #d3caca;
  position: fixed;
  left: 0%;
  top: 0%;
  height: 100%;
  overflow: auto;
  border-style: solid;
  border-color: rgb(26, 25, 25);
  border-width: 5px;
}
.p-dark{
  color: black;
}
.locked{
  fill: rgba(0, 0, 0, 1);
  transform: msFilter;
}
.sidebar a {
  display: block;
  color: black;
  padding: 16px;
  text-decoration: none;
}
 
.sidebar a.active {
  background-color: #7d948b;
  color: white;
}

.sidebar a:hover:not(.active) {
  background-color: rgb(80, 75, 75);
  color: white;
}

div.content {
  margin-left: 200px;
  padding: 1px 16px;
  height: 1000px;
}

@media screen and (max-width: 700px) {
  .sidebar {
    width: 100%;
    height: auto;
    position: relative;
  }
  .sidebar a {float: left;}
  div.content {margin-left: 0;}
}

@media screen and (max-width: 400px) {
  .sidebar a {
    text-align: center;
    float: none;
  }
}
.status-error{
  color: rgb(184, 13, 13);
}
input[type=text], select, textarea {
  font-family: droid-mono-sans;
  width: 100%;
  padding: 12px;
  border: 1px solid #ccc;
  border-radius: 4px;
  resize: vertical;
}
input[type=text]:focus,input[type=text]:hover{
background-color: lightblue;
}
.left{
  position: absolute;
  left: 0%;
  top: 0;
}
.top{
  top:0;
}
nav{
  top: 0%; position: fixed; border-style: solid; background-color: black;
}
.dropdown {
  position: relative;
  display: inline-block;
}

.dropdown-content {
  display: none;
  position: absolute;
  background-color: #a09b9b;
  min-width: 160px;
  box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
  padding: 12px 16px;
  z-index: 1;
}

.dropdown:hover .dropdown-content {
  display: block;
}
.left-nav {
  width: 130px;
  position: fixed;
  z-index: 1;
  top: 20px;
  left: 10px;
  overflow-x: hidden;
  padding: 8px 0;
}
@font-face {
  font-family: droid-mono-sans;
  src: url(font/droid-sans-mono.woff);
  }
  .hidden-div{
    display: none;
  }
  .main-div{
    font-family: droid-mono-sans;
  }
.App-logo {
  height: 40vmin;
  pointer-events: none;
}
.submit:hover {
  background-color: #88a39a;
}
@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}
.btn1{
  font-family: droid-mono-sans;
  background-color: #708b82;
  position: fixed;
  bottom: 0%;
  left: 0%;
  font-size: small;
  color: white;
  padding: 12px 20px;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  float: left;
}
.submit{
  font-family: droid-mono-sans;
  background-color: #708b82;
  color: white;
  padding: 12px 20px;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  float: right;
}
.new-Account{
  position: fixed;
  color:rgb(102, 102, 109);
  bottom: 0%;
left: 0%;
font-family: px-sans;
font-size: xx-small;
}
.new-Account:hover{
  font-style: bold;
  color: #3b4761;
}
.submit-left{
  font-family: droid-mono-sans;
  background-color: #708b82;
  color: white;
  padding: 20px 20px;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}
.App-header {
  font-family: droid-mono-sans;
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}
.App-link {
  color: #61dafb;
}
h3{
  font-family: droid-mono-sans;
}
@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
